* {
	margin: 0;
	padding: 0;
}

ul,
ol {
	list-style: none;
}

a {
	text-decoration: none;
}

header {
	width: 100%;
	margin: 0 auto;
	height: 100px;
	background-color: black;
	color: white;
	position: relative;
}

header h1 {
	width: 100%;
	text-align: center;
	line-height: 60px;
}

header .menu {
	width: 40%;
	min-width: 600px;
	margin: 0 auto;
	height: 40px;
	display: flex;
	justify-content: space-around;
}

header .menu li {
	text-align: center;
	cursor: pointer;
	line-height: 40px;
}

header .my {
	position: absolute;
	right: 20px;
	top: 20px;
	width: 150px;
	display: flex;
	justify-content: space-around;
}

header .my li {
	cursor: pointer;
}
