* {
	margin: 0;
	padding: 0;
}

ul,
ol {
	list-style: none;
}

a {
	text-decoration: none;
}

#wrap {
	width: 100%;
	height: auto;
}

#wrap .main-banner {
	width: 100%;
	height: 1000px;
	background: url(/public/img/bg_01.jpg) no-repeat center;
	background-size: cover;
}

#wrap #best {
	width: 100%;
}

.center {
	width: 90%;
	min-width: 1500px;
	max-width: 1700px;
	margin: 0 auto;
}

#wrap #best .center h2 {
	width: 100%;
	text-align: center;
	font-size: 50px;
	padding-top: 50px;
	font-weight: 600;
}
#wrap #best .center ul {
	padding-top: 10px;
	width: 200px;
	height: 50px;
	line-height: 50px;
	display: flex;
	margin: 0 auto;
	justify-content: space-around;
	font-size: 40px;
	font-weight: 500;
}
#wrap #best .center .product-man {
	padding-top: 20px;
	padding-bottom: 50px;
	width: 100%;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
}

#wrap #best .center .product-man .product-detail {
	width: 20%;
	height: 480px;
	position: relative;
	padding: 10px;
	box-sizing: border-box;
}

#wrap #best .center .product-man .product-detail .img-box {
	width: 100%;
	border-radius: 10px;
	overflow: hidden;
}
#wrap #best .center .product-man .product-detail .img-box img {
	width: 100%;
	object-fit: cover;
	transition: all 0.5s;
}

#wrap #best .center .product-man .product-detail .img-box img:hover {
	transform: scale(1.1);
}
#wrap #best .center .product-man .product-detail span {
	position: absolute;
	bottom: 0;
}

#wrap #best .center .product-woman {
	padding-top: 20px;
	padding-bottom: 50px;
	width: 100%;
	display: none;
	flex-flow: row wrap;
	justify-content: flex-start;
}

#wrap #best .center .product-woman .product-detail {
	width: 20%;
	height: 480px;
	position: relative;
	padding: 10px;
	box-sizing: border-box;
}

#wrap #best .center .product-woman .product-detail .img-box {
	width: 100%;
	border-radius: 10px;
	overflow: hidden;
}
#wrap #best .center .product-woman .product-detail .img-box img {
	width: 100%;
	object-fit: cover;
	transition: all 0.5s;
}

#wrap #best .center .product-woman .product-detail .img-box img:hover {
	transform: scale(1.1);
}
#wrap #best .center .product-woman .product-detail span {
	position: absolute;
	bottom: 0;
}
